import Header from './Header'
import Galleria from './Galleria'
import './Layout.css'

const layoutStyle = {
  margin: 20,
  padding: 20,
  border: '1px solid #DDD'
}

const galleriaTheme = () => (
  Galleria.loadTheme('https://cdnjs.cloudflare.com/ajax/libs/galleria/1.5.7/themes/classic/galleria.classic.min.js').run('.galleria')
)

const Layout = (props) => (
  <div className={'galleria'}>
    <Header />
      {props.children}
    <Galleria onLoad={galleriaTheme}/>
  </div>
)

export default Layout



